export type ChapterTitleType = {
  chapter: string
  conclusion?: string
  chapterPosition: number
  conclusionPosition?: number
}

/**
 *
 * @param contentList - array of strings
 * @param chapterName - capter title
 * @param conclusionName
 * @returns
 */
export const getChaptersData = (
  contentList: string[],
  chapterName: string,
  conclusionName: string
): ChapterTitleType[] => {
  const arr: ChapterTitleType[] = []
  const chapters: ChapterTitleType[] = []
  const chapterRegex = new RegExp(chapterName, 'i')
  const conclusionRegex = new RegExp(conclusionName, 'i')

  for (let i = 1; i < contentList.length; i++) {
    const item = contentList[i]
    const isChapter = item.match(chapterRegex)
    if (isChapter) {
      arr.push({ chapter: item, chapterPosition: i })
    }
  }

  arr.forEach((item) => {
    const isConclusion = item.chapter.match(conclusionRegex)
    if (!isConclusion) {
      chapters.push(item)
    } else {
      chapters[chapters.length - 1].conclusion = item.chapter
      chapters[chapters.length - 1].conclusionPosition = item.chapterPosition
    }
  })

  return chapters
}
