import { PDFDocumentProxy } from 'pdfjs-dist'
import { CONTENT_LIST } from '../../../config/content'
import { getContentListPageNumber } from '../../../helpers/getRefListPageNumber'
import { getPageStringRows } from '../../../utils/parsePdfUtils'
import { ContentListConfigType } from '../../checkPagesFidelity'
import { getListFromStringRows } from './getListFromStringRows'

/**
 *
 * @param pdfData
 * @param setErrors
 * @returns
 */
export const getReferenceList = async (
  pdfData: PDFDocumentProxy,
  contentListConfig: ContentListConfigType,
  constants: typeof CONTENT_LIST
): Promise<string[]> => {
  const pageRefListNumber = getContentListPageNumber(contentListConfig, constants.litList)
  const pageAdditionNumber = getContentListPageNumber(contentListConfig, constants.addition)

  if (typeof pageRefListNumber === 'number') {
    const rows: string[][] = []
    let diff = 0
    if (pageAdditionNumber) {
      diff = pageAdditionNumber - pageRefListNumber
    } else {
      diff = pdfData.numPages - pageRefListNumber
    }
    for (let i = 0; i < diff; i++) {
      const res = await getPageStringRows(pdfData, pageRefListNumber + i)
      if (i === 0 && res[0].toLowerCase() === constants.litList.toLowerCase()) {
        res.shift() // remove title
      }
      if (Number(res[res.length - 1])) res.pop() // in case we need to remove page number
      rows.push(res)
    }

    return getListFromStringRows(rows.flat())
  }
  return []
}
