import type { PDFDocumentProxy } from 'pdfjs-dist'
import { getDocument } from 'pdfjs-dist'
import type { TextContent, TextItem, TextMarkedContent } from 'pdfjs-dist/types/src/display/api'

export async function parsePdfByData(data: Uint8Array | ArrayBuffer | Buffer): Promise<PDFDocumentProxy> {
  const pdfDocument = await getDocument({ data: new Uint8Array(data) }).promise
  return pdfDocument
}

export async function getPageStringObj(pdfDocument: PDFDocumentProxy, pageNumber: number): Promise<TextContent> {
  const page = await pdfDocument.getPage(pageNumber)
  const textContent = await page.getTextContent()
  return textContent
}

export async function getPageStringRows(doc: PDFDocumentProxy, pageNumber: number): Promise<string[]> {
  const page = await doc.getPage(pageNumber)
  const content: TextContent = await page.getTextContent()
  const rows: { text: string; y: number }[] = []
  let currentRow = ''
  let lastY = null
  for (const item of content.items) {
    if ('str' in item) {
      if (lastY === null || item.transform[5] === lastY) {
        currentRow += item.str
      } else {
        rows.push({ text: currentRow, y: lastY })
        currentRow = item.str
      }
      lastY = item.transform[5]
    }
  }
  rows.push({ text: currentRow, y: lastY })
  rows.sort((a, b) => b.y - a.y)
  return rows.map((row) => row.text)
}

export async function getPageString(doc: PDFDocumentProxy, pageNumber: number): Promise<string> {
  const arr = await getPageStringRows(doc, pageNumber)
  return arr.join(' ')
}

export const getFilterObjByTransformHeight = (items: (TextItem | TextMarkedContent)[], height: number): TextItem[] => {
  const res = items.filter((item) => {
    if ('str' in item) {
      return item.transform[5] <= height && !item.hasEOL
    }
    return false
  })
  return res as TextItem[]
}
