import { PDFDocumentProxy } from 'pdfjs-dist'
import { SetErrors } from '../../..'
import { CONTENT_LIST } from '../../config/content'
import { CheckConfig } from '../../config/mainConfig'
import { ERROR_TYPE, PAGES_FIDELITY_KEYS } from '../../errors'
import { getPageStringRows } from '../../utils/parsePdfUtils'
import { checkList } from './helpers/checkList'
import { getContentData } from './helpers/getContentData'
import { getContentList } from './helpers/getContentList'
import { validateTitle } from './helpers/validateTitle'

export type ContentListConfigType = {
  title: string
  page: number
}[]

export const checkPagesFidelity = async ({
  pdfData,
  setErrors,
  isChapterConclusionRequired,
  chapterSize, // todo: add
}: {
  pdfData: PDFDocumentProxy
  setErrors: SetErrors
  isChapterConclusionRequired: CheckConfig['isChapterConclusionRequired']
  chapterSize?: CheckConfig['chapterSize']
}): Promise<ContentListConfigType> => {
  const { listData, listPageNum } = await getContentList(pdfData, CONTENT_LIST)

  // todo: is it possible to be here?
  if (!listData.length) {
    setErrors(ERROR_TYPE.pagesFidelity, PAGES_FIDELITY_KEYS.rule1)
    return []
  }

  const list = getContentData(listData)

  checkList(
    setErrors,
    list.map((item) => item.title),
    CONTENT_LIST,
    isChapterConclusionRequired,
    chapterSize
  )

  for (const item of list) {
    const { title, page } = item

    const pageData = await getPageStringRows(pdfData, page)
    const pageString = pageData.join(' ')
    validateTitle(listPageNum, page, title, pageString, setErrors, CONTENT_LIST)
  }

  return list
}
