import { CheckConfig } from './src/config/mainConfig'
import { ERRORS, ERROR_TYPE, ErrorsType } from './src/errors'
import {
  // checkFrameService,
  checkPagesFidelity,
  checkReferenceList,
  checkReferenceOrder,
} from './src/services'
import { parsePdfByData } from './src/utils/parsePdfUtils'

export type SetErrors = <T extends ERROR_TYPE>(
  type: T,
  rule: keyof (typeof ERRORS)[T],
  str1?: string,
  str2?: string
) => void

export async function check(data: Uint8Array | ArrayBuffer | Buffer, config?: CheckConfig) {
  const results: ErrorsType = {}
  const { refListMinLen, isChapterConclusionRequired = true, chapterSize } = config || {}

  const setErrors: SetErrors = (type, rule, str1 = '', str2 = '') => {
    let str = ERRORS[type][rule] as string
    if (!results[type]) {
      results[type] = { [rule]: [] }
    }
    // @ts-ignore
    if (results[type] && !results[type][rule]) {
      // @ts-ignore
      results[type][rule] = []
    }
    if (str1) {
      str = str.replace('{str1}', str1)
    }
    if (str2) {
      str = str.replace('{str2}', str2)
    }

    // @ts-ignore
    ;(results[type][rule] as string[]).push(str)
  }

  try {
    const pdfData = await parsePdfByData(data)
    const contentListConfig = await checkPagesFidelity({ pdfData, setErrors, isChapterConclusionRequired, chapterSize })
    const { length } = await checkReferenceList(pdfData, contentListConfig, setErrors, refListMinLen)
    await checkReferenceOrder(pdfData, contentListConfig, length, setErrors)
  } catch (e) {
    console.error(e)
  }

  return results
}
