import { SetErrors } from '../../../..'
import { ERROR_TYPE, REF_LIST_KEYS } from '../../../errors'

export const checkListLength = (list: string[], listMinLength: number, setErrors: SetErrors) => {
  if (list.length < listMinLength) setErrors(ERROR_TYPE.referenceList, REF_LIST_KEYS.rule1, list.length.toString())
}
