import { PDFDocumentProxy } from 'pdfjs-dist'
import { SetErrors } from '../../..'
import { CONTENT_LIST } from '../../config/content'
import { CheckConfig } from '../../config/mainConfig'
import { VAK_PATTERNS } from '../../config/reference'
import { ERROR_TYPE, REF_LIST_KEYS } from '../../errors'
import { ContentListConfigType } from '../checkPagesFidelity'
import { checkListLength } from './helpers/checkListLength'
import { checkRefListOrder } from './helpers/checkRefListOrder'
import { checkReferenceElRes } from './helpers/checkReferenceElRes'
import { checkReferenceNumberFormat } from './helpers/checkReferenceNumberFormat'
import { getIsElRes } from './helpers/getIsElRes'
import { getReferenceList } from './helpers/getReferenceList'

export const checkReferenceList = async (
  pdfData: PDFDocumentProxy,
  contentListConfig: ContentListConfigType,
  setErrors: SetErrors,
  listMinLength?: CheckConfig['refListMinLen']
): Promise<{ length: number }> => {
  const referenceList = await getReferenceList(pdfData, contentListConfig, CONTENT_LIST)
  if (typeof listMinLength === 'number') {
    checkListLength(referenceList, listMinLength, setErrors)
  }
  checkRefListOrder(referenceList, setErrors)

  if (!referenceList.length) {
    setErrors(ERROR_TYPE.referenceList, REF_LIST_KEYS.rule0)
  }

  for (const item of referenceList) {
    checkReferenceNumberFormat(setErrors, item)

    if (getIsElRes(item)) {
      checkReferenceElRes(setErrors, item, VAK_PATTERNS)
    }
  }

  return { length: referenceList.length }
}
